.toc {
  padding: 10px;
  top: 100px;
  color: #797979;
  border: 1px solid #d0d0d0;
  min-width: 200px;
  max-width: max-content;
  background: #fff;
}
.toc ol {
  margin: 0;
  padding-left: 10px;
}
.toc li {
  color: #2060d0;
  margin: 10px;
  list-style: none;
}
.toc a:hover {
  cursor: pointer;
  color: #5090ff;
}
