.archive h3 {
  height: 22px;
  line-height: 22px;
}
.archive ul {
  list-style: none;
  padding: 0;
  overflow: hidden;
}
.archive li {
  cursor: pointer;
  margin: 5px 5px;
  height: 30px;
  line-height: 30px;
}
.archive-tag-items li {
  background: #f2f2f2;
  color: #666;
  float: left;
}
.archive-tag-items a {
  display: inline-block;
  height: 30px;
  line-height: 30px;
  padding: 0 10px;
}
.archive-tag-items li:hover {
  background: #333;
  color: #fff;
  text-decoration: none;
}
.archive-list-items a {
  height: 30px;
  display: block;
}
.archive-list-items li:hover {
  opacity: 0.6;
}
.archive-list-items .item-name {
  display: inline-block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.archive-list-items .item-date {
  display: inline-block;
  text-align: right;
  overflow: hidden;
}
@media screen and (max-width: 750px) {
  .item-name {
    padding-right: 5px;
    width: 60%;
  }
  .item-date {
    width: 40%;
  }
}
@media screen and (min-width: 750px) {
  .item-name {
    width: 75%;
  }
  .item-date {
    width: 25%;
  }
}
