html {
  width: 100%;
  height: 100%;
  font-family: 'Noto Serif SC', 'Noto Serif CJK SC', 'Noto Serif CJK', 'Source Han Serif SC', 'Source Han Serif CN', 'Source Han Serif', 'source-han-serif-sc', 'Source Serif Pro', -apple-system, BlinkMacSystemFont, Helvetica Neue, 'Crimson Text', serif;
  color: #444444;
  overflow-y: scroll;
}
* {
  box-sizing: border-box;
}
a:hover,
a:visited,
a:link,
a:active {
  text-decoration: none;
  color: inherit;
}
.container {
  max-width: 800px;
  min-height: 100vh;
  margin: 0 auto;
  position: relative;
}
.content {
  padding: 0 40px;
}
