.article {
  margin-bottom: 60px;
  width: 100%;
  text-align: justify;
}
.article-title {
  font-size: 1.75em;
  text-align: center;
  font-weight: bold;
  color: #000;
}
.article-title a:hover,
.article-title a:link,
.article-title a:visited {
  text-decoration: none;
  color: #000;
}
.article-toc {
  float: right;
  margin: 20px;
}
.article-date {
  color: #797979;
  text-align: center;
  margin: 10px 0;
}
.article-more {
  float: left;
  color: #808080;
  text-decoration: none;
}
.article-more:hover {
  color: #000;
}
.article-tags {
  float: right;
  font-size: 12px;
}
.article-tags-link {
  color: #808080;
  text-decoration: none;
  margin-left: 3px;
}
.article-tags-link:hover {
  color: #000;
}
.article-tags::after {
  clear: both;
}
.article-info {
  height: 24px;
  margin: 20px 0;
  border-bottom: 1px solid #eee;
}
.article-tags-pic {
  margin-right: 5px;
}
.markdown-body {
  box-sizing: border-box;
  min-width: 200px;
  margin: 0 auto;
  padding: 20px 0;
}
.markdown-body a {
  color: #0366d6 !important;
}
@media (max-width: 767px) {
  .markdown-body {
    padding: 0;
  }
}
.post-nav {
  height: 40px;
}
.post-left,
.post-right {
  font-size: 14px;
  line-height: 40px;
  height: 40px;
}
.post-left a,
.post-right a,
.post-left a:visited,
.post-right a:visited {
  color: #797979;
  margin: 0 5px;
}
.post-left a:hover,
.post-right a:hover {
  color: #000;
  text-decoration: underline;
}
.post-left {
  float: left;
}
.post-right {
  float: right;
}
