.nav {
  height: 80px;
  width: 100%;
  max-width: inherit;
  margin: 0 auto;
  display: flex;
  flex-flow: row wrap;
  background: #fff;
}
.nav::after {
  clear: both;
}
.nav-header .nav-sitename {
  font-weight: 900;
  color: #424243;
  text-transform: uppercase;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
}
.nav-menu {
  list-style-type: none;
  padding: 0;
  margin: 0;
}
.nav-menu .nav-item {
  width: 100px;
  cursor: pointer;
  color: #808080;
  line-height: 80px;
  text-align: center;
  display: inline-block;
  padding: 0 10px;
}
.nav-menu .nav-item a:visited {
  color: #808080;
}
.nav-menu .nav-item a:hover {
  font-weight: bold;
  color: #000;
}
@media screen and (max-width: 750px) {
  .nav {
    justify-content: center;
    padding: 10px;
  }
}
@media screen and (min-width: 750px) {
  .nav {
    justify-content: space-between;
    padding: 0 40px;
  }
}
.taijiBox {
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  margin-left: 10px;
  position: relative;
  top: 3px;
}
.taiji {
  display: flex;
  align-items: center;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: linear-gradient(black 50%, white 0);
  animation: r 2s linear infinite;
}
.taiji:before,
.taiji:after {
  flex: 1;
  content: '';
  height: 5px;
  border: solid 5px black;
  border-radius: 50%;
  background: white;
  animation: s 1s ease-in-out infinite alternate;
}
@keyframes s {
  to {
    transform: scale(1.5);
  }
}
@keyframes r {
  to {
    transform: rotate(1turn);
  }
}
.taiji:before {
  background: black;
  height: 5px;
  border: solid 5px white;
  transform-origin: 0% 50%;
  transform: scale(0.5);
}
.taiji:after {
  transform-origin: 100% 50%;
  transform: scale(0.5);
  animation-delay: -1s;
}
